'use client';

import { ReactElement } from 'react';
import { FormattedMessage } from 'react-intl';

import { Tooltip, TooltipContent } from '@yandex-music-int/sol/components/Tooltip';
import { Caption } from '@yandex-music-int/sol/components/Typography';

import styles from './FutureFeature.module.scss';

interface Props {
    children: ReactElement;
    title?: string;
}

const TOOLTIP_OPEN_DELAY = 200;
const TOOLTIP_CLOSE_DELAY = 0;

export const FutureFeature = ({ title, children }: Props): ReactElement => {
    return (
        <Tooltip
            offsetOptions={4}
            placement="top"
            hoverSettings={{
                delay: {
                    open: TOOLTIP_OPEN_DELAY,
                    close: TOOLTIP_CLOSE_DELAY
                }
            }}
        >
            {children}
            <TooltipContent className={styles.root}>
                <div className={styles.text}>
                    <Caption variant="span" type="text" size="s" weight="bold">
                        {title}
                    </Caption>
                    <Caption variant="span" type="text" size="s" weight="normal" className={styles.description}>
                        <FormattedMessage id="future-feature.message" />
                    </Caption>
                </div>
            </TooltipContent>
        </Tooltip>
    );
};
