'use client';

import { clsx } from 'clsx';
import { ReactElement, useCallback, MouseEvent, useMemo } from 'react';
import { useIntl } from 'react-intl';

import { Button, ButtonProps } from '@yandex-music-int/sol/components/Button';
import { Icon, IconProps } from '@yandex-music-int/sol/components/Icon';

import { WithChildren, WithClassName } from '../../lib/index.js';

import styles from './PlayButton.module.scss';

interface Props extends WithClassName, WithChildren {
    iconSize?: IconProps['size'];
    radius?: ButtonProps['radius'];
    size?: ButtonProps['size'];
    variant?: 'filled' | 'default';
    color?: ButtonProps['color'];
    buttonVariant?: ButtonProps['variant'];
    isPlaying?: boolean;
    iconClassName?: string;
    disabled?: boolean;
    onClick?: () => void;
    withHover?: boolean;
    withRipple?: boolean;
}

export const PlayButton = ({
    iconSize = 'xs',
    className,
    size,
    variant = 'default',
    isPlaying = false,
    onClick,
    iconClassName,
    disabled,
    color,
    buttonVariant = 'text',
    children,
    radius = 'round',
    withHover,
    withRipple = false
}: Props): ReactElement => {
    const { formatMessage } = useIntl();
    const iconVariant = `${isPlaying ? 'pause' : 'play'}${variant === 'filled' ? '_filled' : ''}` as const;
    const ariaLabel = useMemo(() => {
        return isPlaying ? formatMessage({ id: 'player-actions.pause' }) : formatMessage({ id: 'player-actions.play' });
    }, [formatMessage, isPlaying]);
    const handleOnClick = useCallback(
        (event: MouseEvent<HTMLButtonElement>) => {
            onClick?.();
            event.stopPropagation();
            event.preventDefault();
        },
        [onClick]
    );

    return (
        <Button
            className={clsx(styles.root, styles.button, className)}
            variant={buttonVariant}
            color={color}
            radius={radius}
            size={size}
            flexIcon
            withRipple={withRipple}
            aria-label={ariaLabel}
            onClick={handleOnClick}
            icon={<Icon variant={iconVariant} size={iconSize} className={iconClassName} />}
            disabled={disabled}
            withHover={withHover}
        >
            {children}
        </Button>
    );
};
