'use client';

import { clsx } from 'clsx';
import { throttle } from 'lodash-es';
import { ReactElement, useMemo, useState } from 'react';
import { GridComponents, VirtuosoGridProps, ListRange } from 'react-virtuoso';

import { WithClassName } from '../../lib/types/WithClassName.js';

import { List } from './List.js';
import { Scroller } from './Scroller.js';
import { VirtualGridScroll, Props as VirtualGridScrollProps } from './VirtualGridScroll.js';
import styles from './VirtualScroll.module.scss';

export interface VirtualGridProps extends WithClassName, VirtualGridScrollProps {
    customComponents?: GridComponents<unknown>;
    itemClassName?: string;
    itemContentCallback: (itemIndex: number) => ReactElement | undefined;
    listClassName?: string;
    onGetDataByPage?: (page: number) => void;
    onGetDataByRange?: (range: ListRange) => void;
    overscan?: VirtuosoGridProps<unknown>['overscan'];
    pageSize?: number;
    totalCount: number;
    //     VirtualGrid
    totalRequests?: number;
    debounceDurationInMs?: number;
    initialItemCount?: number;
    minInitialItemCount?: number;
}

const PAGE_SIZE = 20;
const THROTTLE_DURATION_MS = 100;
const DEFAULT_MIN_INITIAL_ITEMS_COUNT = 20;

const overscanSettings = 700;

export const VirtualScroll = ({
    className,
    customComponents,
    onGetDataByPage,
    onGetDataByRange,
    itemClassName,
    itemContentCallback,
    listClassName,
    overscan = overscanSettings,
    pageSize = PAGE_SIZE,
    totalCount,
    totalRequests,
    debounceDurationInMs,
    initialItemCount,
    minInitialItemCount = DEFAULT_MIN_INITIAL_ITEMS_COUNT,
    ...props
}: VirtualGridProps): ReactElement => {
    const [isScrolling, setIsScrolling] = useState(false);

    const handleScroll = useMemo(() => {
        return throttle((scrollingState: boolean) => {
            setIsScrolling(scrollingState);
        }, THROTTLE_DURATION_MS);
    }, []);

    const components = useMemo(
        () => ({
            Scroller,
            List,
            ...(customComponents || {})
        }),
        // eslint-disable-next-line react-hooks/exhaustive-deps
        [customComponents, totalRequests]
    );
    //     , .    initialItemCount     CPU
    const actualInitialItemCount = initialItemCount ? Math.min(initialItemCount, minInitialItemCount) : undefined;

    return (
        <div
            className={clsx(styles.root, className, {
                [styles.root_scrolling]: isScrolling,
                [styles.root_notScrolling]: !isScrolling
            })}
        >
            <VirtualGridScroll
                overscan={overscan}
                components={components}
                listClassName={listClassName}
                itemClassName={itemClassName}
                isScrolling={handleScroll}
                itemContent={itemContentCallback}
                totalCount={totalCount}
                pageSize={pageSize}
                onPageHandler={onGetDataByPage}
                onRangeHandler={onGetDataByRange}
                debounceDurationInMs={debounceDurationInMs}
                initialItemCount={actualInitialItemCount}
                {...props}
            />
        </div>
    );
};
