'use client';

import { clsx } from 'clsx';
import { chunk } from 'lodash-es';
import { forwardRef, Ref, ReactElement, useMemo, useState, useId, useRef } from 'react';

import { Carousel } from '@yandex-music-int/sol/components/Carousel';

import { WithClassName, TrackVariant } from '../../lib/index.js';
import { BlockHeader } from '../BlockHeader/BlockHeader.js';
import { CarouselControls } from '../CarouselControls/CarouselControls.js';
import { TracksList } from '../TracksList/TracksList.js';

import styles from './TracksBlock.module.scss';

enum Columns {
    ONE = 'one',
    TWO = 'two'
}

interface Props extends WithClassName {
    children?: ReactElement[];
    isLoading?: boolean;
    title?: string;
    description?: string;
    viewAllActionLink?: string | null;
    coverUrl?: string;
    variant?: TrackVariant;
    forwardRef?: Ref<HTMLDivElement>;
    maxColumns?: 1 | 2;
    itemsPerColumn?: number;
    containerClassName?: string;
    itemClassName?: string;
}

const TracksBlockShimmer = (maxColumns: number, variant: TrackVariant, itemsPerColumn: number): ReactElement[] => {
    return Array.from({ length: maxColumns }, (value: unknown, index: number) => {
        return <TracksList isLoading shimmersCount={itemsPerColumn} key={index} variant={variant} />;
    });
};

const TracksBlockComponent = ({
    forwardRef: ref,
    isLoading,
    title,
    description,
    viewAllActionLink,
    coverUrl,
    variant = TrackVariant.PLAYLIST,
    children,
    className,
    maxColumns = 2,
    itemsPerColumn = 4,
    containerClassName,
    itemClassName
}: Props): ReactElement => {
    const [countColumns, setCountColumns] = useState<Columns | undefined>();
    const labelId = useId();
    const carouselRef = useRef<HTMLOListElement | null>(null);

    const tracks = useMemo((): ReactElement[] => {
        const columns = chunk(children, itemsPerColumn).slice(0, maxColumns);

        if (columns.length === 1) {
            setCountColumns(Columns.ONE);
        } else {
            setCountColumns(Columns.TWO);
        }

        return columns.map((column: ReactElement[], index: number) => (
            <TracksList key={index} variant={variant} className={styles.trackColumn}>
                {column}
            </TracksList>
        ));
    }, [children, itemsPerColumn, maxColumns, variant]);

    return (
        <section ref={ref} className={clsx(styles.root, className)}>
            <BlockHeader
                className={containerClassName}
                labeledForId={labelId}
                title={title}
                description={description}
                coverUrl={coverUrl}
                viewAllActionLink={viewAllActionLink}
                controls={<CarouselControls className={styles.controls} carouselRef={carouselRef} />}
            />
            <Carousel
                itemClassName={clsx(
                    styles.item,
                    styles[`item_columns_${isLoading && maxColumns > 1 ? Columns.TWO : countColumns}`],
                    itemClassName
                )}
                containerClassName={styles.content}
                ref={carouselRef}
                aria-labelledby={labelId}
            >
                {isLoading ? TracksBlockShimmer(maxColumns, variant, itemsPerColumn) : tracks}
            </Carousel>
        </section>
    );
};

export const TracksBlock = forwardRef<HTMLDivElement, Props>((props: Props, ref: Ref<HTMLDivElement>) => (
    <TracksBlockComponent forwardRef={ref} {...props} />
));
