'use client';

import { clsx } from 'clsx';
import { ReactElement, useCallback, MouseEvent } from 'react';
import { useIntl } from 'react-intl';

import { Button, ButtonProps } from '@yandex-music-int/sol/components/Button';
import { Icon, IconProps } from '@yandex-music-int/sol/components/Icon';

import { WithClassName } from '../../lib/index.js';

import styles from './DislikeButton.module.scss';

interface Props extends WithClassName {
    variant?: 'text' | 'default';
    iconClassName?: string;
    isDisliked?: boolean;
    onClick?: () => void;
    iconSize?: IconProps['size'];
    size?: ButtonProps['size'];
}

export const DislikeButton = ({
    className,
    variant = 'text',
    isDisliked = false,
    onClick,
    iconClassName,
    iconSize,
    size = 's'
}: Props): ReactElement => {
    const { formatMessage } = useIntl();
    const iconType = isDisliked ? 'disliked' : 'dislike';

    const handleOnClick = useCallback(
        (event: MouseEvent<HTMLButtonElement>) => {
            onClick?.();
            event.stopPropagation();
        },
        [onClick]
    );

    return (
        <Button
            className={clsx(styles.root, className)}
            withRipple={false}
            variant={variant}
            size={size}
            radius="round"
            aria-label={formatMessage({ id: 'interface-actions.do-not-like' })}
            aria-pressed={isDisliked}
            onClick={handleOnClick}
            icon={<Icon size={iconSize} className={iconClassName} variant={iconType} />}
        />
    );
};
