'use client';

import { ReactElement, useCallback, useState } from 'react';

import { Button } from '@yandex-music-int/sol/components/Button';

import { CONTAINER_COOKIE_STORAGE, useContainer, StorageKeys } from '../../lib/index.js';

import styles from './DevPanel.module.scss';
import { useFuture } from './useFutureFeatures.js';

const ToggleRTL = (): ReactElement => {
    const [isRTL, setIsRTL] = useState<boolean>(false);

    const handler = useCallback(() => {
        setIsRTL(!isRTL);

        document.documentElement.setAttribute('dir', isRTL ? 'ltr' : 'rtl');
    }, [isRTL, setIsRTL]);

    return (
        <Button size="xxs" className={styles.block} variant="default" block onClick={handler}>
              {isRTL ? 'LTR' : 'RTL'}
        </Button>
    );
};

const ToggleNavigation = (): ReactElement => {
    const container = useContainer();
    const cookies = container.get(CONTAINER_COOKIE_STORAGE);
    const isWebNextNavigationOn = cookies.get(StorageKeys.WebNextNavigation) === 'on';

    const handler = useCallback(() => {
        cookies.set(StorageKeys.WebNextNavigation, isWebNextNavigationOn ? 'default' : 'on');
        if (typeof window !== 'undefined') {
            window.location.reload();
        }
    }, [isWebNextNavigationOn, cookies]);

    return (
        <Button size="xxs" className={styles.block} variant="default" block onClick={handler}>
            {isWebNextNavigationOn ? '' : ''}  
        </Button>
    );
};

const ToggleFuture = (): ReactElement => {
    const { isFuture, toggleFuture } = useFuture();

    return (
        <Button size="xxs" className={styles.block} variant="default" block onClick={toggleFuture}>
            {isFuture ? '' : ''}  
        </Button>
    );
};

const Panel = (): ReactElement => {
    return (
        <div className={styles.panel}>
            <ToggleRTL />
            {process.env.BUILD_TYPE === 'web' && <ToggleNavigation />}
            <ToggleFuture />
        </div>
    );
};

export const DevPanel = (): ReactElement => {
    const [isVisible, setIsVisible] = useState<boolean>(false);
    const togglePanel = useCallback(() => setIsVisible(!isVisible), [isVisible]);

    return (
        <div className={styles.root}>
            <Button size="xxs" onClick={togglePanel}>
                dev
            </Button>
            {isVisible && <Panel />}
        </div>
    );
};
