'use client';

import { ReactElement } from 'react';
import { useIntl } from 'react-intl';

import { Button } from '@yandex-music-int/sol/components/Button';
import { Icon } from '@yandex-music-int/sol/components/Icon';
import { Caption } from '@yandex-music-int/sol/components/Typography';

import styles from './CardLikes.module.scss';

interface Props {
    isLiked: boolean;
    likesCount: number;
    handleOnLikeClick: () => Promise<void>;
}

export const CardLikes = ({ isLiked, likesCount, handleOnLikeClick }: Props): ReactElement<Props> => {
    const { formatNumber } = useIntl();
    const iconVariant = isLiked ? 'likedVariant' : 'likeVariant';

    return (
        <Button
            className={styles.root}
            onClick={handleOnLikeClick}
            variant="text"
            withRipple={false}
            icon={<Icon variant={iconVariant} size="xxs" className={styles.icon} />}
        >
            <Caption variant="div" size="s" type="entity" weight="medium">
                {formatNumber(likesCount)}
            </Caption>
        </Button>
    );
};
