'use client';

import { useEffect, useState, useCallback } from 'react';

import { CONTAINER_CONFIG_TOKEN, CONTAINER_LOCAL_STORAGE, useContainer } from '../../lib/index.js';

const FUTURE_FLAG_NAME = 'future';

interface UseFuture {
    isFuture: boolean;
    toggleFuture(): void;
}

export const useFuture = (): UseFuture => {
    const container = useContainer();
    const storage = container.get(CONTAINER_LOCAL_STORAGE);
    const config = container.get(CONTAINER_CONFIG_TOKEN);
    const [isFuture, setIsFuture] = useState<boolean>(false);

    useEffect(() => {
        const isEnabled = config.dev.panel && Boolean(storage.get(FUTURE_FLAG_NAME));

        setIsFuture(isEnabled);
    }, [config.dev.panel, storage]);

    const toggleFuture = useCallback((): void => {
        storage.set(FUTURE_FLAG_NAME, !isFuture);
        window.location.reload();
    }, [storage, isFuture]);

    return {
        isFuture,
        toggleFuture
    };
};
