import { clsx } from 'clsx';
import { ReactElement } from 'react';

import { Shimmer } from '@yandex-music-int/sol/components/Shimmer';

import { WithClassName, WithAria } from '@/shared/lib/index.js';

import styles from './EntityCardShimmer.module.scss';

interface Props extends WithClassName, WithAria {
    round?: boolean;
    centered?: boolean;
}

export const EntityCardShimmer = ({ className, round, 'aria-label': ariaLabel, centered }: Props): ReactElement => {
    return (
        <div tabIndex={0} aria-label={ariaLabel} aria-live="polite" aria-busy className={clsx(styles.root, className)}>
            <Shimmer className={clsx(styles.cover, { [styles.cover_round]: round })} radius="l" />
            <div
                className={clsx(styles.infoContainer, {
                    [styles.infoContainer_centered]: centered
                })}
            >
                <Shimmer className={styles.title} radius="s" />
            </div>
        </div>
    );
};
