import { clsx } from 'clsx';
import { Ref, ReactElement, forwardRef } from 'react';
import { GridComponents } from 'react-virtuoso';

import styles from './VirtualScroll.module.scss';

export type ScrollerComponentProps = GridComponents['Scroller'] & { forwardRef?: Ref<HTMLDivElement> };

const ScrollerComponent = ({ style, forwardRef: ref, ...props }: ScrollerComponentProps): ReactElement => {
    return (
        <div
            className={clsx(styles.scroller, styles.scroller_withFooter)}
            style={{ ...style }}
            ref={ref}
            {...props}
            tabIndex={-1}
        />
    );
};

export const Scroller = forwardRef<HTMLDivElement, ScrollerComponentProps>(
    (props: ScrollerComponentProps, ref: Ref<HTMLDivElement>) => <ScrollerComponent forwardRef={ref} {...props} />
);
