import { ReactElement, Ref, forwardRef } from 'react';

import { type ScrollerComponentProps } from './Scroller.js';

const ListComponent = ({ style, forwardRef: ref, ...props }: ScrollerComponentProps): ReactElement => {
    return (
        <div
            aria-labelledby="virtual-grid-header"
            role="region"
            style={{ ...style }}
            ref={ref}
            {...props}
            tabIndex={0}
        />
    );
};

export const List = forwardRef<HTMLDivElement, ScrollerComponentProps>(
    (props: ScrollerComponentProps, ref: Ref<HTMLDivElement>) => <ListComponent forwardRef={ref} {...props} />
);
