'use client';

import { clsx } from 'clsx';
import { observer } from 'mobx-react-lite';
import { ReactElement } from 'react';
import { FormattedMessage, useIntl } from 'react-intl';

import { Caption } from '@yandex-music-int/sol/components/Typography';

import { dateToYYYYProps, useStore, WithClassName } from '../../lib/index.js';
import { Link } from '../Link/Link.js';

import styles from './Footer.module.scss';

interface Props extends WithClassName {}

export const Footer = observer(({ className }: Props): ReactElement => {
    const { settings } = useStore();
    const { formatMessage, formatDate } = useIntl();

    // TODO:  query  ?lang=${lang}
    const agreementUrl =
        process.env.BUILD_TYPE === 'desktop'
            ? `https://yandex.${settings.clientTld}/legal/music_mobile_agreement/`
            : `https://yandex.${settings.clientTld}/legal/music_termsofuse`;

    return (
        <footer className={clsx(styles.root, className)}>
            <div className={styles.links}>
                <ol className={styles.list}>
                    <li className={styles.item}>
                        <Link
                            target="_blank"
                            // TODO:  query  ?lang=${lang}
                            href={`https://yandex.${settings.clientTld}/support/music/performers-and-copyright-holders.html`}
                            className={styles.link}
                        >
                            <Caption type="controls" variant="span" size="s" weight="medium">
                                <FormattedMessage id="footer.links-copyright-holders" />
                            </Caption>
                        </Link>
                    </li>
                    <li className={styles.item}>
                        <Link target="_blank" href={agreementUrl} className={styles.link}>
                            <Caption type="controls" variant="span" size="s" weight="medium">
                                <FormattedMessage id="footer.links-terms" />
                            </Caption>
                        </Link>
                    </li>
                    <li className={styles.item}>
                        <Link
                            target="_blank"
                            // TODO:  query  ?lang=${lang}
                            href={`https://music.yandex.${settings.clientTld}/legal/recommendations/ru/#music`}
                            className={styles.link}
                        >
                            <Caption type="controls" variant="span" size="s" weight="medium">
                                <FormattedMessage id="footer.links-recommendation-rules" />
                            </Caption>
                        </Link>
                    </li>
                    <li className={styles.item}>
                        <Link
                            target="_blank"
                            // TODO:  query  ?lang=${lang}
                            href={`https://yandex.${settings.clientTld}/support/music/index.html`}
                            className={styles.link}
                        >
                            <Caption type="controls" variant="span" size="s" weight="medium">
                                <FormattedMessage id="footer.links-help" />
                            </Caption>
                        </Link>
                    </li>
                </ol>
                <Caption
                    variant="span"
                    type="text"
                    size="s"
                    weight="normal"
                    className={styles.explicitText}
                    tabIndex={0}
                    dangerouslySetInnerHTML={{ __html: formatMessage({ id: 'footer.explicit-content' }) }}
                />
            </div>
            <div className={styles.copyrights}>
                <Caption variant="span" type="text" size="s" weight="medium" className={styles.text}>
                    &copy; {formatDate(new Date(), dateToYYYYProps())} &#160;
                    <Link
                        target="_blank"
                        href={`https://ya.${settings.clientTld}`}
                        className={clsx(styles.copyrightLink, styles.yandexMusicLink)}
                    >
                        <FormattedMessage id="footer.yandex-music" />
                    </Link>
                </Caption>
                <Link
                    target="_blank"
                    // TODO:  query  ?lang=${lang}
                    href={`https://yandex.${settings.clientTld}/all`}
                    className={styles.copyrightLink}
                >
                    <Caption variant="span" type="text" size="s" weight="medium">
                        <FormattedMessage id="footer.yandex-project" />
                    </Caption>
                </Link>
            </div>
        </footer>
    );
});
