'use client';

import { ComponentType, FC, ReactElement } from 'react';

import { CONTAINER_LOGGER_TOKEN, useContainer, WithChildren } from '@/shared/lib/index.js';

export interface ErrorContainerProps extends WithChildren {
    fallback: ((props: Record<string, unknown>) => ReactElement) & { displayName: string };
}

function getDisplayName<T>(WrappedComponent: ComponentType<T>): string {
    return WrappedComponent.displayName || WrappedComponent.name || 'Component';
}

export function withLoggerContainer<TP, TS extends ErrorContainerProps>(WrappedComponent: ComponentType<TP>): FC<TS> {
    const WithContainer: FC<TS> = (props: TS) => {
        const container = useContainer();
        const logger = container.get(CONTAINER_LOGGER_TOKEN);

        return (
            // @ts-ignore   ,       HOC
            <WrappedComponent {...props} logger={logger} />
        );
    };

    WithContainer.displayName = `withContainer(${getDisplayName(WrappedComponent)})`;

    return WithContainer;
}
