'use client';

import { ReactNode, use } from 'react';

import { CONTAINER_CONFIG_TOKEN, getContainer } from '@/shared/lib/index.js';

interface Props {
    nonce: string;
    requestId: string;
    hostTld: string;
    assetPrefix: string;
    env: typeof process.env.NEXT_PUBLIC_CONFIG_ENV;
}

export const RumScript = ({ nonce, requestId, hostTld, assetPrefix, env }: Props): ReactNode => {
    if (typeof window !== 'undefined') {
        return null;
    }

    const container = use(getContainer(hostTld, env));
    const config = container.get(CONTAINER_CONFIG_TOKEN);

    if (!config.rumSettings) {
        return null;
    }

    const { rumId, project, service, platform, heroElement, page, version, environment } = config.rumSettings;

    const callRumInit = `initRum({
        version: window.VERSION || '${version}',
        platform: window.PLATFORM || '${platform}',
        environment: '${environment}',
        heroElement: '${heroElement}',
        page: ${page},
        project: '${project}',
        regionId: '213',
        requestId: '${requestId}',
        rumId: '${rumId}',
        sendClientUa: true,
        service: '${service}',
        testIds: []
    })`;

    return (
        <>
            <script id="rum-script" src={`${assetPrefix}/rumScript.js`} nonce={nonce} />
            <script dangerouslySetInnerHTML={{ __html: callRumInit }} nonce={nonce} />
        </>
    );
};
