'use client';

import { clsx } from 'clsx';
import { ReactElement, useCallback, MouseEvent } from 'react';
import { useIntl } from 'react-intl';

import { useToggle } from '@yandex-music-int/sol/common/hooks/useToggle.js';
import { Button, ButtonProps } from '@yandex-music-int/sol/components/Button';
import { Icon, IconProps } from '@yandex-music-int/sol/components/Icon';

import { WithChildren, WithClassName } from '../../lib/index.js';

import styles from './LikeButton.module.scss';

interface Props extends WithClassName, WithChildren {
    variant?: 'text' | 'default';
    iconClassName?: string;
    isLiked?: boolean;
    onClick?: () => void;
    iconSize?: IconProps['size'];
    size?: ButtonProps['size'];
    withRipple?: boolean;
}

export const LikeButton = ({
    className,
    variant = 'text',
    iconSize,
    isLiked = false,
    onClick,
    iconClassName,
    withRipple = false,
    size = 's',
    children
}: Props): ReactElement => {
    const {
        state: likedAnimation,
        toggleFalse: stopLikedAnimation,
        toggleTrue: startLikedAnimation
    } = useToggle(false);
    const {
        state: unlikedAnimation,
        toggleFalse: stopUnlikedAnimation,
        toggleTrue: startUnlikedAnimation
    } = useToggle(false);

    const { formatMessage } = useIntl();
    const iconType = isLiked ? 'liked' : 'like';

    const animationEndedHandler = useCallback(() => {
        stopLikedAnimation();
        stopUnlikedAnimation();
    }, [stopLikedAnimation, stopUnlikedAnimation]);

    const handleOnClick = useCallback(
        (event: MouseEvent<HTMLButtonElement>) => {
            onClick?.();
            if (isLiked) {
                startUnlikedAnimation();
            } else {
                startLikedAnimation();
            }
            event.stopPropagation();
            event.preventDefault();
        },
        [isLiked, onClick, startLikedAnimation, startUnlikedAnimation]
    );

    return (
        <Button
            className={clsx(
                styles.root,
                {
                    [styles.root_liked]: likedAnimation,
                    [styles.root_unliked]: unlikedAnimation
                },
                className
            )}
            onAnimationEnd={animationEndedHandler}
            withRipple={withRipple}
            variant={variant}
            size={size}
            radius="xxxl"
            aria-label={formatMessage({ id: 'interface-actions.like' })}
            aria-pressed={isLiked}
            onClick={handleOnClick}
            icon={<Icon size={iconSize} className={clsx(styles.icon, iconClassName)} variant={iconType} />}
        >
            {children}
        </Button>
    );
};
