import { ReactElement } from 'react';

import { TrackVariant, WithClassName } from '@/shared/lib/index.js';

import { AlbumTrackShimmer } from './AlbumTrackShimmer/AlbumTrackShimmer.js';
import { PlaylistTrackShimmer } from './PlaylistTrackShimmer/PlaylistTrackShimmer.js';

interface Props extends WithClassName {
    variant: TrackVariant;
}

export const TrackShimmer = ({ variant, className }: Props): ReactElement => {
    switch (variant) {
        case TrackVariant.PLAYLIST:
            return <PlaylistTrackShimmer className={className} />;

        case TrackVariant.ALBUM:
            return <AlbumTrackShimmer className={className} />;
    }
};
