'use client';

import { observer } from 'mobx-react-lite';
import { default as NextLink } from 'next/link.js';
import { ReactElement } from 'react';

import { Link as BaseLink, LinkProps as BaseLinkProps } from '@yandex-music-int/sol/components/Link';

import { useSafePlatformLink } from '../../lib/index.js';

export const Link = observer(({ href, ...props }: BaseLinkProps): ReactElement => {
    const { href: safeHref, target, rel } = useSafePlatformLink(href);

    // @ts-expect-error
    return <BaseLink component={<NextLink prefetch />} href={safeHref} target={target} rel={rel} {...props} />;
});
