'use client';

import { clsx } from 'clsx';
import { ReactElement, useRef, useCallback, useMemo, ReactNode } from 'react';

import { Icon } from '@yandex-music-int/sol/components/Icon';
import { Image } from '@yandex-music-int/sol/components/Image';
import { Paper } from '@yandex-music-int/sol/components/Paper';
import { Caption, Heading } from '@yandex-music-int/sol/components/Typography';

import { WithClassName } from '../../lib/types/WithClassName.js';
import { Link } from '../Link/Link.js';

import styles from './BlockHeader.module.scss';

interface Props extends WithClassName {
    labeledForId?: string;
    title?: string;
    description?: string;
    coverUrl?: string;
    viewAllActionLink?: string | null;
    controls?: ReactNode;
}

export const BlockHeader = ({
    className,
    coverUrl,
    labeledForId,
    title,
    description,
    viewAllActionLink,
    controls
}: Props): ReactElement => {
    const previewRef = useRef<HTMLImageElement | null>(null);

    const handlePreviewClick = useCallback((): void => {
        if (previewRef.current && 'focus' in previewRef.current) {
            previewRef.current.focus();
        }
    }, []);

    const titleComponent = useMemo(() => {
        if (title && viewAllActionLink) {
            return (
                <Link
                    className={styles.title}
                    icon={<Icon className={styles.titleIcon} size="xs" variant="arrowRight" />}
                    iconPosition="right"
                    href={viewAllActionLink}
                >
                    <Heading id={labeledForId} className={styles.heading} variant="h3" size="m" weight="bold">
                        {title}
                    </Heading>
                </Link>
            );
        }
        return (
            <div className={styles.title}>
                <Heading id={labeledForId} className={styles.heading} variant="h3" size="m" weight="bold">
                    {title}
                </Heading>
            </div>
        );
    }, [labeledForId, title, viewAllActionLink]);

    return (
        <div className={clsx(styles.root, styles.blockHeader, className)}>
            <div className={styles.start}>
                {coverUrl && (
                    <Paper radius="s" className={styles.coverContainer}>
                        <Image
                            src={coverUrl}
                            ref={previewRef}
                            alt={title}
                            onClick={handlePreviewClick}
                            fit="cover"
                            withAvatarReplace
                        />
                    </Paper>
                )}
                <div className={styles.textContainer}>
                    {titleComponent}
                    {description && (
                        <Caption
                            id={`${labeledForId}-description`}
                            variant="span"
                            type="text"
                            size="m"
                            weight="medium"
                            className={styles.description}
                        >
                            {description}
                        </Caption>
                    )}
                </div>
            </div>
            {controls}
        </div>
    );
};
