'use client';

import { debounce } from 'lodash-es';
import { useMemo, ReactElement } from 'react';
import { VirtuosoGrid, ListRange, VirtuosoGridProps } from 'react-virtuoso';

const RANGE_CHANGE_DEBOUNCE_MS = 100;

export interface Props extends VirtuosoGridProps<unknown> {
    debounceDurationInMs?: number;
    pageSize?: number;
    onPageHandler?: (page: number) => void;
    onRangeHandler?: (range: ListRange) => void;
}

export const VirtualGridScroll = ({
    pageSize,
    onPageHandler,
    onRangeHandler,
    debounceDurationInMs = RANGE_CHANGE_DEBOUNCE_MS,
    totalCount = 0,
    ...props
}: Props): ReactElement => {
    const handleRangeChanged = useMemo(() => {
        return debounce((range: ListRange) => {
            onRangeHandler?.(range);
            if (pageSize && onPageHandler) {
                const currentPage = Math.floor(range.endIndex / pageSize);
                const nextPage = currentPage + 1;
                const prevPage = Math.floor(range.startIndex / pageSize);

                for (let index = prevPage; index < nextPage; index++) {
                    onPageHandler(index);
                }
            }
        }, debounceDurationInMs);
    }, [pageSize, onPageHandler, onRangeHandler, debounceDurationInMs]);

    return <VirtuosoGrid rangeChanged={handleRangeChanged} totalCount={totalCount} {...props} />;
};
