'use client';

// @ts-expect-error        next,    es-modules
import Script from 'next/script';
import { ReactNode, useCallback } from 'react';

import { useMetrika } from '@/shared/lib/analytics/useMetrika.js';
import { CONTAINER_CONFIG_TOKEN, useContainer } from '@/shared/lib/index.js';

interface Props {
    nonce: string;
}

export const MetrikaScript = ({ nonce }: Props): ReactNode => {
    const container = useContainer();
    const config = container.get(CONTAINER_CONFIG_TOKEN);
    const metrika = useMetrika();

    const onScriptLoaded = useCallback(() => {
        metrika.init(config.metrikaSettings);
    }, [config.metrikaSettings, metrika]);

    if (!config.metrikaSettings) {
        return null;
    }

    return (
        <Script id="metrika-script" src="https://mc.yandex.ru/metrika/tag.js" nonce={nonce} onReady={onScriptLoaded} />
    );
};
