'use client';

import { clsx } from 'clsx';
import { MutableRefObject, ReactElement } from 'react';

import { useCarouselSwipe } from '@yandex-music-int/sol/common/hooks/useCarouselSwipe.js';
import { Button } from '@yandex-music-int/sol/components/Button';
import { Icon } from '@yandex-music-int/sol/components/Icon';

import { WithClassName } from '../../lib/types/WithClassName.js';

import styles from './CarouselControls.module.scss';

interface Props extends WithClassName {
    carouselRef?: MutableRefObject<HTMLOListElement | null>;
}

export const CarouselControls = ({ carouselRef, className }: Props): ReactElement => {
    const { swipeBackward, swipeForward } = useCarouselSwipe(carouselRef);

    return (
        <div className={clsx(styles.root, className)}>
            <Button
                tabIndex={-1}
                aria-hidden
                className={styles.control}
                onClick={swipeBackward}
                size="xxxs"
                radius="round"
                variant="outline"
                withRipple={false}
                icon={<Icon size="xxs" variant="arrowLeft" />}
            />
            <Button
                tabIndex={-1}
                aria-hidden
                className={styles.control}
                onClick={swipeForward}
                size="xxxs"
                radius="round"
                variant="outline"
                withRipple={false}
                icon={<Icon size="xxs" variant="arrowRight" />}
            />
        </div>
    );
};
