'use client';

import { clsx } from 'clsx';
import { ReactElement, useMemo } from 'react';
import { FormattedMessage } from 'react-intl';

import { Image } from '@yandex-music-int/sol/components/Image';
import { Paper } from '@yandex-music-int/sol/components/Paper';
import { Caption } from '@yandex-music-int/sol/components/Typography';

import { WithClassName } from '../../../lib/types/WithClassName.js';
import { Link } from '../../Link/Link.js';
import { BaseNotification } from '../BaseNotification.js';

import styles from './BaseNotificationLike.module.scss';

type EntityVariant = 'artist' | 'album' | 'playlist' | 'track';

interface Props extends WithClassName {
    onClose?: () => void;
    coverUri?: string;
    entityVariant: EntityVariant;
    entityUrl?: string;
    collectionUrl: string;
    entityTitle?: string;
    isLiked: boolean;
}

const entitiesMap: Record<EntityVariant, ReactElement> = {
    album: <FormattedMessage id="entity-names.album" />,
    artist: <FormattedMessage id="entity-names.artist" />,
    playlist: <FormattedMessage id="entity-names.playlist" />,
    track: <FormattedMessage id="entity-names.track" />
};

export const BaseNotificationLike = ({
    onClose,
    entityVariant,
    entityUrl,
    collectionUrl,
    entityTitle,
    coverUri,
    isLiked,
    className
}: Props): ReactElement => {
    const isArtist = entityVariant === 'artist';
    const isTrack = entityVariant === 'track';

    const cover = useMemo(() => {
        return (
            <Paper className={styles.cover} radius={isArtist ? 'round' : 's'}>
                <Image
                    className={styles.image}
                    src={coverUri}
                    size={100}
                    fit="cover"
                    alt={entityTitle}
                    withAvatarReplace
                />
            </Paper>
        );
    }, [coverUri, entityTitle, isArtist]);

    const title = useMemo(() => {
        if (entityUrl && entityTitle) {
            return (
                <Link className={styles.link} href={entityUrl}>
                    <Caption className={styles.title} variant="span" type="controls" size="m" lineClamp={1}>
                        {entityTitle}
                        &nbsp;
                    </Caption>
                </Link>
            );
        }

        return;
    }, [entityTitle, entityUrl]);

    const actionTextId = useMemo(() => {
        if (isLiked) {
            if (isTrack) {
                return 'notifications-info.added-to-playlist';
            }

            return 'notifications-info.added-to';
        }

        if (isTrack) {
            return 'notifications-info.removed-from-playlist';
        }

        return 'notifications-info.removed-from';
    }, [isLiked, isTrack]);

    const collectionTargetText = useMemo(() => {
        if (isLiked) {
            if (isTrack) {
                return (
                    <>
                        &#8220;
                        <FormattedMessage id="entity-names.liked-playlist" />
                        &#8221;
                    </>
                );
            }

            return <FormattedMessage id="notifications-info.to-collection" />;
        }

        if (isTrack) {
            return (
                <>
                    &#8220;
                    <FormattedMessage id="entity-names.liked-playlist" />
                    &#8221;
                </>
            );
        }

        return <FormattedMessage id="notifications-info.from-collection" />;
    }, [isLiked, isTrack]);

    const text = useMemo(() => {
        return (
            <Caption className={styles.text} variant="div" type="controls" size="m">
                {entitiesMap[entityVariant]}
                &nbsp;
                {title}
                <FormattedMessage id={actionTextId} />
                &nbsp;
                <Link className={styles.link} href={collectionUrl}>
                    <Caption variant="span" type="controls" size="m" lineClamp={1}>
                        {collectionTargetText}
                    </Caption>
                </Link>
            </Caption>
        );
    }, [actionTextId, collectionTargetText, collectionUrl, entityVariant, title]);

    return (
        <BaseNotification
            className={clsx(styles.root, className)}
            message={
                <div className={styles.message}>
                    {cover}
                    {text}
                </div>
            }
            onClose={onClose}
        />
    );
};
