'use client';

import { clsx } from 'clsx';
import { forwardRef, Ref, ReactElement, useId, useRef } from 'react';

import { Carousel } from '@yandex-music-int/sol/components/Carousel';
import { Shimmer } from '@yandex-music-int/sol/components/Shimmer';

import { WithClassName } from '@/shared/lib/index.js';

import { BlockHeader } from '../BlockHeader/BlockHeader.js';
import { CarouselControls } from '../CarouselControls/CarouselControls.js';
import { CarouselShimmer } from '../Shimmer/CarouselShimmer/CarouselShimmer.js';

import styles from './CarouselBlock.module.scss';

interface Props extends WithClassName {
    children?: ReactElement[];
    isLoading?: boolean;
    title?: string;
    description?: string;
    coverUrl?: string;
    viewAllActionLink?: string | null;
    containerClassName?: string;
    headerClassName?: string;
    forwardRef?: Ref<HTMLDivElement>;
    showHeaderShimmer?: boolean;
}

const CarouselBlockComponent = ({
    forwardRef: ref,
    isLoading,
    title,
    description,
    coverUrl,
    viewAllActionLink,
    children,
    className,
    containerClassName,
    headerClassName,
    showHeaderShimmer = false
}: Props): ReactElement => {
    const id = useId();
    const carouselRef = useRef<HTMLOListElement | null>(null);

    return (
        <section ref={ref} className={clsx(styles.root, className)}>
            {showHeaderShimmer && isLoading ? (
                <Shimmer className={styles.shimmerTitle} radius="l" />
            ) : (
                <BlockHeader
                    className={clsx(containerClassName, headerClassName)}
                    labeledForId={id}
                    title={title}
                    description={description}
                    coverUrl={coverUrl}
                    viewAllActionLink={viewAllActionLink}
                    controls={<CarouselControls className={styles.controls} carouselRef={carouselRef} />}
                />
            )}
            <Carousel
                containerClassName={clsx(styles.content, containerClassName)}
                ref={carouselRef}
                itemClassName={styles.item}
                aria-labelledby={`${id} ${id}-description`}
            >
                {isLoading ? CarouselShimmer() : children}
            </Carousel>
        </section>
    );
};

export const CarouselBlock = forwardRef<HTMLDivElement, Props>((props: Props, ref: Ref<HTMLDivElement>) => (
    <CarouselBlockComponent forwardRef={ref} {...props} />
));
