'use client';

import { ReactNode, use } from 'react';

import { CONTAINER_CONFIG_TOKEN, getContainer } from '@/shared/lib/index.js';

interface Props {
    nonce: string;
    hostTld: string;
    assetPrefix: string;
    env: typeof process.env.NEXT_PUBLIC_CONFIG_ENV;
}

export const ErrorBoosterScript = ({ nonce, hostTld, assetPrefix, env }: Props): ReactNode => {
    if (typeof window !== 'undefined') {
        return null;
    }

    const container = use(getContainer(hostTld, env));
    const config = container.get(CONTAINER_CONFIG_TOKEN);

    if (!config.logger?.errorBooster) {
        return null;
    }

    const { project, platform, unhandledRejection, uncaughtException, resourceFails, page, version, environment } =
        config.logger.errorBooster;

    const callErrorBoosterInit = `initErrorBooster({
        version: window.VERSION || '${version}',
        platform: window.PLATFORM || '${platform}',
        environment: '${environment}',
        page: ${page},
        project: '${project}',
        regionId: '213', // TODO    
        unhandledRejection: ${unhandledRejection},
        uncaughtException: ${uncaughtException},
        resourceFails: ${resourceFails}
    })`;

    return (
        <>
            <script id="error-booster-script" src={`${assetPrefix}/errorBoosterScript.js`} nonce={nonce} />
            <script dangerouslySetInnerHTML={{ __html: callErrorBoosterInit }} nonce={nonce} />
        </>
    );
};
