import { clsx } from 'clsx';
import { ReactElement, ReactNode } from 'react';

import { TrackVariant } from '../../lib/types/TrackVariant.js';
import { WithChildren } from '../../lib/types/WithChildren.js';
import { WithClassName } from '../../lib/types/WithClassName.js';
import { TrackShimmer } from '../Shimmer/TrackShimmer/TrackShimmer.js';

import styles from './TracksList.module.scss';

interface Props extends WithClassName, WithChildren {
    variant: TrackVariant;
    isLoading?: boolean;
    shimmersCount?: number;
}

const TracksListShimmer = (variant: TrackVariant, shimmersCount: number = 5): ReactNode => {
    return Array.from({ length: shimmersCount }, (value: unknown, index: number) => (
        <TrackShimmer key={index} variant={variant} className={styles.shimmer} />
    ));
};

export const TracksList = ({ variant, isLoading, shimmersCount, className, children }: Props): ReactElement => {
    return (
        <div className={clsx(styles.root, className)}>
            {!isLoading ? children : TracksListShimmer(variant, shimmersCount)}
        </div>
    );
};
