'use client';

import { Component, ErrorInfo } from 'react';

import { Logger } from '@yandex-music-int/logger';

import { ErrorContainerProps, withLoggerContainer } from './withLoggerContainer.js';

interface ErrorBoundaryProps extends ErrorContainerProps {
    logger: Logger;
}

export interface ErrorBoundaryState {
    hasError: boolean;
    error?: Error;
}

class ErrorBoundaryClass extends Component<ErrorBoundaryProps, ErrorBoundaryState> {
    public static getDerivedStateFromError(error: Error): ErrorBoundaryState {
        return { hasError: true, error };
    }

    public override state: ErrorBoundaryState = {
        hasError: false
    };

    public override componentDidCatch(error: Error, errorInfo: ErrorInfo): void {
        this.props.logger.error(error, { additional: errorInfo, type: 'error-boundary' });
    }

    public override render(): React.ReactNode {
        const { hasError } = this.state;
        const { fallback: FallbackComponent, children } = this.props;

        if (hasError) {
            return <FallbackComponent />;
        }
        return children;
    }
}

const ErrorBoundary = withLoggerContainer(ErrorBoundaryClass);

export { ErrorBoundary };
