'use client';

import { clsx } from 'clsx';
import { ReactElement, useMemo } from 'react';
import { useIntl } from 'react-intl';

import { Shimmer } from '@yandex-music-int/sol/components/Shimmer';

import { WithClassName } from '@/shared/lib/index.js';

import styles from './PlaylistTrackShimmer.module.scss';

interface Props extends WithClassName {}

export const PlaylistTrackShimmer = ({ className }: Props): ReactElement => {
    const { formatMessage } = useIntl();
    const ariaLabel = useMemo(() => {
        return formatMessage(
            { id: 'loading-messages.entity-is-loading' },
            { entityName: formatMessage({ id: 'entity-names.track' }) }
        );
    }, [formatMessage]);

    return (
        <div tabIndex={0} aria-label={ariaLabel} aria-live="polite" aria-busy className={clsx(styles.root, className)}>
            <div className={styles.infoContainer}>
                <Shimmer className={styles.cover} radius="s" />
                <div className={styles.textContainer}>
                    <Shimmer className={styles.title} radius="l" />
                </div>
            </div>
            <Shimmer className={styles.action} radius="l" />
        </div>
    );
};
